body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


/* list component ==============================================================================*/

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-top: 20px
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}
/* End of list component ==============================================================================*/

/* Button Animation from hover css =============================================== */
.butn {
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  background: #e1e1e1;
  transition-property: color;
  transition-duration: 0.3s;
}

.butn:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #2098D1;
  transform: scaleY(0);
  transform-origin: 50%;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

#delete-btn:before {
  background: red;
}

#view-btn{
  text-decoration: none; font: menu;
    display: inline-block; padding: 4px 8px;
    background: ButtonFace; color: ButtonText;
    border-style: solid; border-width: 2px;
    border-color: ButtonHighlight ButtonShadow ButtonShadow ButtonHighlight;
}
#view-btn:active{
  border-color: ButtonShadow ButtonHighlight ButtonHighlight ButtonShadow;
}

.butn:hover, .butn:focus, .butn:active{
  color: white;
}

.butn:hover:before, .butn:focus:before, .butn:active:before {
  transform: scaleY(1);
}

/* Search button ======================================== */

.btn {
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  transition-duration: 0.3s;
  transition-property: transform;
}
.btn:hover, .btn:focus, .btn:active {
  transform: scale(1.1);
}
/* end of button animate=============================================== */

.book-image{
  float: left;
  padding-right: 5px;
}
